import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useSpring, animated } from 'react-spring'
import { motion } from 'framer-motion'

interface AnimatedLinkProps {
  opacity: number
  transform: string
}

const Start = () => {
  const [clicked, setClicked] = useState(false)
  const navigate = useNavigate()

  const handleAnimationEnd = () => {
    // アニメーション終了時に画面遷移
    navigate('/nemo2023sample/Home')
  }

  const { transform, opacity } = useSpring<AnimatedLinkProps>({
    transform: clicked ? 'scale(8.0)' : 'scale(1)',
    opacity: clicked ? 0.0 : 1,
    config: { tension: 100, friction: 20, duration: 400 },
    onRest: handleAnimationEnd,
  })

  // クリックイベントハンドラーの型を修正
  const handleClick = (e: React.MouseEvent<HTMLDivElement>) => {
    // アニメーションを開始
    setClicked(true)
  }

  return (
    // npm install framer-motion
    <motion.div
      initial={{ scale: 1.5, opacity: 0 }}
      animate={{ scale: 1, opacity: 1 }}
      exit={{ scale: 1, opacity: 0 }}
      transition={{ duration: 0.5 }}
      className=""
    >
      <animated.div
        style={{ transform, opacity }}
        onClick={handleClick}
        className="zoom-out-image flex h-screen cursor-pointer items-center  justify-center"
      >
        <Link
          to="/nemo2023/Home"
          style={{ pointerEvents: 'none' }}
          className="transform rounded-full bg-nemoBlue p-4 text-center text-white transition-transform duration-300 hover:scale-105 xl:p-6 xl:text-3xl"
        >
          <div className="">ネモた、誕生日おめでとう！</div>
          <div className="mt-2 text-black xl:text-2xl">（扉絵になる予定）</div>
        </Link>
      </animated.div>
    </motion.div>
  )
}

export default Start
