import React, { useState } from 'react'
import { useSpring, animated } from 'react-spring'

const MsgArray = () => {
  const msgImage = [
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
    '/nemo2023sample/img/message/messageSample.png',
  ]

  const [selectedImage, setSelectedImage] = useState<string | null>(null)
  const [scroll, setScroll] = useState<string>('visible')

  document.body.style.overflow = scroll

  const openModal = (index: number) => {
    setSelectedImage(msgImage[index])
    setScroll('hidden')
  }

  const closeModal = () => {
    setSelectedImage(null)
    setScroll('visible')
  }

  // const modalAnimation = useSpring({
  //   opacity: selectedImage ? 1 : 0,
  //   transform: `scale(${selectedImage ? 1 : 0.5})`,
  // });

  const scaleAnimation = useSpring({
    transform: `scale(${selectedImage ? 1 : 0.5})`,
  })

  const backgroundAnimation = useSpring({
    opacity: selectedImage ? 1 : 0,
    backgroundColor: selectedImage ? 'rgba(0, 0, 0, 0.75)' : 'rgba(0, 0, 0, 0)',
  })

  return (
    <>
      {msgImage.map((image, index) => (
        <img
          key={index}
          src={image}
          alt={`msgImage ${index}`}
          className="transform cursor-pointer shadow transition-transform duration-300 hover:scale-105"
          onClick={() => openModal(index)}
        />
      ))}

      {selectedImage && (
        <div>
          <animated.div
            style={backgroundAnimation}
            className="fixed left-0 top-0 flex h-full w-full items-center justify-center"
            onClick={closeModal}
          >
            {/* <img
              src={selectedImage}
              alt={`selectedImage ${msgImage}`}
              className=" h-1/2"
            /> */}
            <animated.img
              src={selectedImage}
              alt="Selected Image"
              className="aspect-square px-4 sm:h-3/4"
              style={scaleAnimation}
            />
          </animated.div>
        </div>
      )}
    </>
  )
}

export default MsgArray
