import React from 'react'
import TitleBar from './parts/TitleBar'
import { Link } from 'react-router-dom'
import YouTubeVideo from './parts/YouTubeVideo'

const Home = () => {
  return (
    <div className=" relative  h-screen text-center">
      <TitleBar />
      <YouTubeVideo />
      <div className="  items-center justify-center pt-6 lg:flex">
        <Link to={'/nemo2023sample/Illust'}>
          <IllustIcon />
        </Link>
        <Link to={'/nemo2023sample/Message'}>
          <MessageIcon />
        </Link>
        <Link to={'/nemo2023sample/TimeLine'}>
          <TLIcon />
        </Link>
      </div>
      （アイコン用イラストを置く予定）
    </div>
  )
}

export default Home

const IllustIcon = () => {
  return (
    <div className="m-2  transform bg-nemoBlue p-8 text-center text-white shadow transition-transform duration-300 hover:scale-105 xl:text-3xl ">
      イラスト
    </div>
  )
}

const MessageIcon = () => {
  return (
    <div className="m-2 transform bg-nemoBlue  p-8 text-center text-white shadow transition-transform duration-300 hover:scale-105 xl:text-3xl">
      メッセージ
    </div>
  )
}

const TLIcon = () => {
  return (
    <div className="m-2 transform bg-nemoBlue  p-8 text-center text-white shadow transition-transform duration-300 hover:scale-105 xl:text-3xl">
      イベント
    </div>
  )
}
