import React from 'react'
import Youtube from 'react-youtube'

const YouTubeVideo = () => {
  //const videoId = "Ci_zad39Uhw?si=y9vaQ0zLrvmTNRHF";
  const videoId = 'dtd34KfBkpQ?si=7bTo5qFk1a3chTcj'
  const opts = {
    width: '100%',
    height: '100%',

    playerVars: {
      playsinline: 0,
      // autoplay: 1,
      loop: 1,
      rel: 0,
    },
  }

  return (
    <div className="mx-auto flex justify-center pt-2 lg:w-3/5  xl:pt-6 ">
      <Youtube
        videoId={videoId}
        opts={opts}
        className="aspect-video  w-full rounded-xl px-3  py-3   shadow-md "
      />
    </div>
  )
}

export default YouTubeVideo
