import React, { useState } from 'react'
import { useSpring, animated } from 'react-spring'

import illust_1 from '../../img/Illust_1.jpg'
import illust_2 from '../../img/Illust_2.jpg'
import illust_3 from '../../img/Illust_3.jpg'
import illust_4 from '../../img/Illust_4.jpg'
import illust_5 from '../../img/Illust_5.jpg'
import illust_6 from '../../img/Illust_6.jpg'
import illust_7 from '../../img/Illust_7.jpg'
import illust_8 from '../../img/Illust_8.jpg'
import illust_9 from '../../img/Illust_9.jpg'
import illust_10 from '../../img/Illust_10.jpg'
import illust_11 from '../../img/Illust_11.jpg'
import illust_12 from '../../img/Illust_12.jpg'
import 残機 from '../../img/残機.png'

const IllustArray = () => {
  const IllustBox = [
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
    残機,
  ]
  const samuneImage = [
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
    '/nemo2023sample/img/samune/残機_サムネ.png',
  ]

  const link = [
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
    'https://youtube.com/shorts/of_LrR8JxKw?si=SwfLxhlHqzsBYVMr',
  ]

  const name = [
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
    '残機',
  ]

  const [selectedImage, setSelectedImage] = useState<string | null>(null)
  const [selectedIndex, setSelectedIndex] = useState<number>(0)
  const [scroll, setScroll] = useState<string>('visible')

  document.body.style.overflow = scroll

  const openModal = (index: number) => {
    setSelectedImage(IllustBox[index])
    setSelectedIndex(index)
    setScroll('hidden')
  }

  const closeModal = () => {
    setSelectedImage(null)
    setScroll('visible')
  }

  const youtubeLink = (index: number) => {
    window.open(link[index], '_blank')
  }

  const scaleAnimation = useSpring({
    transform: `scale(${selectedImage ? 1 : 0.5})`,
  })

  const backgroundAnimation = useSpring({
    opacity: selectedImage ? 1 : 0,
    backgroundColor: selectedImage ? 'rgba(0, 0, 0, 0.75)' : 'rgba(0, 0, 0, 0)',
  })

  return (
    <>
      {samuneImage.map((image, index) => (
        <div className="rounded-xl bg-blue-100 object-contain text-center shadow">
          <img
            key={index}
            src={image}
            alt={`samuneImage ${index}`}
            className=" aspect-ratio: 9 / 16 rounded-transition transform rounded-t-xl shadow transition-transform duration-300 hover:scale-105"
            onClick={() => openModal(index)}
          />
          <button
            onClick={() => youtubeLink(index)}
            className="my-2 w-full transform rounded-full bg-nemoBlue py-2  text-xs text-white  shadow transition-transform duration-300 hover:scale-105 hover:bg-sky-400 xl:w-4/5 xl:text-base xl:font-bold"
          >
            ▶ {name[index]}
          </button>
        </div>
      ))}

      {selectedImage && (
        <div>
          <animated.div
            style={backgroundAnimation}
            className="fixed left-0 top-0 flex h-full w-full items-center justify-center bg-black bg-opacity-75 text-white "
            onClick={closeModal}
          >
            <div className="flex h-screen w-fit flex-col items-center justify-center px-2 leading-8">
              <animated.img
                style={scaleAnimation}
                src={selectedImage}
                alt={`selectedImage`}
                className="sm:h-3/4 "
              />
              <button
                onClick={() => youtubeLink(selectedIndex)}
                className="my-2 w-full transform rounded-full bg-nemoBlue px-4  py-2 text-white shadow transition-transform duration-300 hover:scale-105 hover:bg-sky-400 xl:font-bold"
              >
                Listen to ▶ {name[selectedIndex]}
              </button>
            </div>
          </animated.div>
        </div>
      )}
    </>
  )
}

export default IllustArray
